@use "global" as *;

.p-news {
  background-color: $brown6;
    padding-top: calc((100/375) *100vw );
    padding-bottom: calc((100/375) *100vw );
    @include mq(md) {
      padding-top: calc((120/1440) *100vw );
      padding-bottom: calc((120/1440) *100vw );
    }
}

.p-news__inner {
  max-width: myrem(600);
  @include mq(md) {
    max-width: myrem(1000);
    margin-inline: auto;
  }
}

.p-news__title {
  text-align: center;
}

.p-news__item {
  border-bottom: 1px solid $black;
}

.p-news__items {
  margin-top: myrem(40);
  margin-right: auto;
  @include mq(md) {
   margin-top: myrem(50);
  }
}

.p-news__item:not(:first-of-type) {
  margin-top: myrem(24);
}


.p-news__item-link {
  display: flex;
  flex-direction: column;
  @include mq(md) {
    flex-direction: row;
    transition:  .3s;
    align-items: center;
  }
}

.p-news__meta-box {
  display: flex;
  align-items: center;
  gap: myrem(20);
  @include mq(md) {
    gap: myrem(48);
  }
}

.p-news__meta {
  background-color: $brown;
  color: $white;
  font-size: myrem(12);
  padding: myrem(1) myrem(20);
    @include mq(md) {
    font-size: myrem(12); 
  }
}

.p-news__date{
  color: $black;
  font-size: myrem(16);
  letter-spacing: 0.1em;
  @include mq(md) {

  }
}

.p-news__text {
  letter-spacing: 0.1em;
  font-size: myrem(14);
  padding-block: myrem(22);
  @include mq(md) {
    margin-left: myrem(48);
    font-size: myrem(16);   
    transition: .3s;
  }
}

.p-news__button {
  margin-top: myrem(50);
  text-align: center;
  @include mq(md) {
    margin-top: myrem(80);
  }
}


